.project-wrapper {
  margin: 0;
  padding: 0;
  background-color: #4d4d4d;
  height: 100%;
  width: 100%;
}


.project-page {
  display: grid; 

  grid-template-rows: 60px 14% 40% 10%;
  grid-template-columns: 1fr 1fr;
  
  gap: 0px;
  height: 100%;
}

.project-name {
  grid-row-start: 1;
  grid-column-start: 1;

  grid-row-end: 2;
  grid-column-end: 3;
  /* margin-bottom: 30px; */

  color: white;
  font-size: 35px;
  text-align: left;

  
  margin-left: 5%;
  padding-bottom: 15%;
  margin-bottom: 15%;
}


#rectangle-small-left{
  grid-row-start: 2;
  grid-column-start: 1;

  grid-row-end: 3;
  grid-column-end: 2;
  background-color: white;
  height: 80%;
  width: 80%;
  margin: auto;
  text-align: center;
  border-radius: 40px;
}

#rectangle-small-right{
  grid-row-start: 2;
  grid-column-start: 2;

  grid-row-end: 3;
  grid-column-end: 3;
  background-color: white;
  height: 80%;
  width: 80%;
  margin: auto;
  text-align: center;
  border-radius: 40px;
}

#rectangle-large-top{
  grid-row-start: 3;
  grid-column-start: 1;

  grid-row-end: 4;
  grid-column-end: 3;
  background-color: white;
  height: fit-content;
  width: 90%;
  margin: auto;
  margin-top: 10px;
  text-indent: 20px;
  border-radius: 10px;
}

#rectangle-large-bottom{
  grid-row-start: 4;
  grid-column-start: 1;

  grid-row-end: 5;
  grid-column-end: 3;
  background-color: white;
  height: fit-content;
  width: 90%;
  margin: auto;
  margin-top: 10px;
  border-radius: 10px;
  text-align: center;
}

.bug-report-btn {
  height: 30px;
  margin-top: 30px;
  text-align: center;
  padding: auto;

  border-radius: 10px;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: white;
  border-color: #2e2e2e;
}

#upcomingTask{
  text-align: center;
  grid-row-start: 3;
  grid-column-start: 2;

  grid-row-end: 4;
  grid-column-end: 2;

  
}

#managers{
  grid-row-start: 5;
  grid-column-start: 1;

  grid-row-end: 6;
  grid-column-end: 2;


  display: inline-block;

  margin-top: auto;
  width: 45%;
}

#developers{
  grid-row-start: 5;
  grid-column-start: 1;

  grid-row-end: 6;
  grid-column-end: 2;

  display: inline-block;

  margin: auto;
  padding-bottom: 50px;
  width: 45%;
}

.dev-dropdown{
  border-radius: 10px;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: white;
  border-color: #2e2e2e;
}

.dev-labels{
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: bold;
  font-size: 25px;
}

.dev-form-header{
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: bold;
  font-size: 35px;
}

